.customRounded{
  border-radius: 15px 50px 0px 0px;
  border:0;

}

.cardRounded{
  border-radius: 15px 50px 10px 10px;
  border:0;

}

.spaceTop{
  margin-top: 70px;
}

.customButton{
  background-color: #ee9b00;
  border-color: #ee9b00;
  outline-color: #ee9b00;
  color: #001219;
  height: 60px;
  width: 200px;
  font-size: 1.4rem;
}

.customButton:hover{
  background-color: #005f73;
  border-color: #005f73;
  outline-color: #005f73;
  color: white;
  animation: wobble 1s 1;
}


@keyframes wobble {
  25% {
    transform: rotate(5deg);
  }
  50% {
    transform: rotate(-5deg);
  }
  75% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

